<template xmlns="http://www.w3.org/1999/html">
  <div id="page-content" class="header-static">
    <div class>
      <form id="contact-form">
        <div class="submit-area padding-sm">
          <div class="row no-margin padding-onlytop-sm center">
            <div class="inline-block margin-lr-s text-right">
              <div>
                岔首A高程
                <br />
                <input type="number" v-model="aHeight" />
              </div>
              <div>
                分段数m
                <br />
                <input type="number" v-model="m" />
              </div>
            </div>
            <div class="inline-block margin-lr-s text-right">
              <div>
                岔尾B高程
                <br />
                <input type="number" v-model="bHeight" />
              </div>
              <div>
                分段数n
                <br />
                <input type="number" v-model="n" />
              </div>
            </div>
            <div class="inline-block margin-lr-s text-right">
              <div>
                延长线终点C高程
                <br />
                <input type="number" v-model="cHeight" />
              </div>
              <div>
                水平偏差Q
                <br />
                <input type="number" v-model="Q" />
              </div>
            </div>

            <div class="row no-margin center">
              <div>结果</div>
              <div class="inline-block margin-lr-lg margin-top-m align-top">
                <div v-bind:key="i" v-for="i in resultM">
                  <span v-text="i.name"></span>
                  <sub v-text="i.sub"></sub>=
                  <span v-text="i.value"></span>
                </div>
              </div>
              <div class="inline-block margin-lr-lg margin-top-m align-top">
                <div v-bind:key="i" v-for="i in resultN">
                  <span v-text="i.name"></span>
                  <sub v-text="i.sub"></sub>=
                  <span v-text="i.value"></span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      aHeight: 0,
      m: 1,
      bHeight: 0,
      n: 1,
      cHeight: 0,
      Q: 0,
      resultM: [],
      resultN: [],
    }
  },
  watch: {
    aHeight: function () {
      this.calc()
    },
    m: function () {
      this.calc()
    },
    bHeight: function () {
      this.calc()
    },
    n: function () {
      this.calc()
    },
    cHeight: function () {
      this.calc()
    },
    Q: function () {
      this.calc()
    },
  },
  computed: {},
  methods: {
    calc: function () {
      this.resultM = []
      var difference = this.aHeight - this.bHeight
      this.resultM.push({
        name: 'Q',
        sub: '',
        value: this.Q,
      })
      this.resultM.push({
        name: 'A',
        sub: '',
        value: this.Q + this.aHeight,
      })
      for (var i = 1; i <= this.m - 1; i++) {
        this.resultM.push({
          name: 'm',
          sub: i,
          value: (this.Q + this.aHeight - (difference / this.m) * i).toFixed(3),
        })
      }
      this.resultM.push({
        name: 'B',
        sub: '',
        value: this.Q + this.bHeight,
      })
      this.resultN = []
      var difference2 = this.bHeight - this.cHeight
      this.resultN.push({
        name: 'Q',
        sub: '',
        value: this.Q,
      })
      this.resultN.push({
        name: 'B',
        sub: '',
        value: this.Q + this.bHeight,
      })
      for (var i = 1; i <= this.n - 1; i++) {
        this.resultN.push({
          name: 'n',
          sub: i,
          value: (this.Q + this.bHeight - (difference2 / this.n) * i).toFixed(
            3
          ),
        })
      }
      this.resultN.push({
        name: 'C',
        sub: '',
        value: this.Q + this.cHeight,
      })
    },
  },
}
</script>
<style scoped>
.container {
  width: 80%;
  margin: auto;
}
* {
  font-size: 13px;
}
.input-height {
  height: 64px;
}

.center {
  text-align: center;
}

.align-top {
  vertical-align: top;
}

.margin-bottom {
  margin-bottom: 15px;
}

.text-right {
  text-align: right;
}
input {
  width: 60px;
}
.margin-lr-s {
  margin-left: 5px;
  margin-right: 5px;
}
</style>

